#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{    
    position: absolute;    
    top: -2rem;    
    right: 1rem;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}
.introContent{    
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;    
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
    font-size: 1rem;
    font-weight: 200;
}
.introName{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 2rem;
    padding: 0 5px;
}
.btn{
    background: #fff;
    margin: 1rem 0;
    padding: 0.75rem 2rem;
    border: none;
    border-radius: 2rem;
    
}
@media screen and (max-width:840px) {
        .bg{
            right: -10vw;
        }
        .introContent{
            font-size: 10vw;
        }
        .hello{
            font-size: 4.5vw;
        }
}
@media screen and (max-width:480px) {
    .bg{
        right: -20vw;
    }
   
}