#skills{
    overflow: hidden;
    width: 100vw;
    max-width: 65rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}
.skillTitle{
    font-size: 3rem;
    font-weight: 600;
    padding: 3rem auto;
    margin-bottom: 1.5rem;
}
.skillDesc{
    font-size: 1rem;
    font-weight: 400;
    max-width: 50rem;
    padding: 0 2rem;
}
.skillBars{
    margin: 1.5rem;
    width: 100vw;
    max-width: 80%;
    text-align: left;
}
.skillBar{
    display: flex;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background:rgb(50,50,50)
}
.skillbarImg{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}
.skillBarText>p{
    font-size: 0.9rem;
    font-weight: 100;
}
@media screen and (max-width:720px) {
    .skillBarText>p{
        font-size: 2vw;
    }
}
@media screen and (max-width:480px) {
    .skillBarText>p{
        font-size: 3vw;
    }
    .skillbarImg{
        height: 2.25rem;
        width: 2.25rem;
    }
    .skillBarText>h2{
        font-size: 5vw;
    }
}